import React, { useContext, useEffect, useState } from 'react';
import { Divider, IconButton, List, ListItem, ListItemAvatar, ListItemText, Tooltip, Typography } from "@mui/material";
import { Box } from "@mui/system";

import Avatar from '@mui/material/Avatar';
import Badge from '@mui/material/Badge';
import PersonIcon from '@mui/icons-material/Person';
import CommentIcon from '@mui/icons-material/Comment';

//  Importancion de contextos
import { MultiplayerContext } from '../../../../context/MultiplayerContext'

function UserAvatar({ userName = 'SIN NOMBRE', id, messagesCount, isOpenDrawerChat, handleToggleDrawer}) {

    const { setChatData, chatData, connectedUsers, setConnectedUsers  } = useContext(MultiplayerContext);

    function openChatWindow(){
        //  Cambio chatData en el contexto para cargar los mensajes del usuario con ese id
        setChatData(id);

        // abro el drawer de chat
        handleToggleDrawer('drawerChat', true);
        restarthMessagesCount();
    }

    const restarthMessagesCount = () =>{
        //  Hago una copia de los datos
        let usersDataCopy = connectedUsers.length>0? [...connectedUsers] : [];
        const user = usersDataCopy.find(user => user.id === id);
        if(user){
            //  Pongo en 0 el conteo de mensajes nuevos
            user.messagesCount = 0;
            setConnectedUsers(usersDataCopy);
            // console.log('datos nuevo', usersDataCopy);
        }
    }

    useEffect(()=>{
        // console.log('aumento mensajes')
        // if(isOpenDrawerChat && chatData.id === id){
        //     console.log('chat abierto')
        //     restarthMessagesCount();
        // }
    },[connectedUsers.find(user => user.id === id)?.messagesCount]);

    return (
        <ListItem
            secondaryAction={
                <Tooltip title="Enviar mensaje">
                    <IconButton onClick={openChatWindow} edge="end" aria-label="delete">
                        <Badge color="secondary" badgeContent={connectedUsers.find(user => user.id === id)?.messagesCount}>
                            <CommentIcon />
                        </Badge>  
                    </IconButton>
                    
                </Tooltip>

            }
        >
            <ListItemAvatar>
                <Avatar>
                    <PersonIcon />
                </Avatar>
            </ListItemAvatar>
            <ListItemText
                primary={userName}
                secondary={'ID: ' + id}
            />
        </ListItem>
    );
}

export default function ListaUsuarios({ handleToggleDrawer, isOpenDrawerChat }) {

    const { connectedUsers } = useContext(MultiplayerContext);

    // useEffect(()=> {
    //     console.log('lista de usuarios',connectedUsers)
    // }, [connectedUsers]);
    
    return (
        <Box sx={{ height: '100%' }}>
            <Typography variant="overline" display="block" gutterBottom>
                {connectedUsers.length + ' usuarios conectados'}
            </Typography>
            <Divider />
            <List sx={{ height: '95%', overflow: 'hidden', overflowY: 'auto' }} id="chat-window-messages">
                {/* <UserAvatar userName='Juan Carlos Cuasquén' /> */}
                {connectedUsers.map((user, i) => 
                    <UserAvatar key={i} 
                        userName={user.userName} 
                        id={user.id} 
                        messagesCount={user.messagesCount}
                        isOpenDrawerChat={isOpenDrawerChat} 
                        handleToggleDrawer={handleToggleDrawer}/>
                )}
                
            </List>


        </Box>
    );
}