/**
 * Contexto de React donde se almacenan los datos del usuario y los temas de la aplicación
 * útil para guardar los datos de personalización y temas de la app
 * Irá creciendo a medida que los datos necesarios para la app se incrementen
 * 
 * Los datos que representa cada variable de hoyostools en el localstorage se describen en el archivo .env
 */

/**
 * POR HACER
 * Crear un estado separado para cada dato: nombre || avatar || ropa
 * 
 */

import React, { createContext, useEffect, useRef, useState } from 'react';

//  importo la página de carga a visualizarse mientras se consultan y validan los datos
import LoadingPanel from '../components/react-components/LoadingPanel/LoadingPanel';

export const emptypreferenciasUsuario = {
  preferenciasUsuario: {
    nombre: '',
    avatar: '',
    ropa: {}
  }
}

export const UserDataContext = createContext(emptypreferenciasUsuario);

export function UserDataProvider({ children }) {

  //  Declaración de estados y seters
  const [allUserDataVerified, setAllUserDataVerified] = useState(false); // Trigger para mandar una página de carga mientras se verifican todos los datos
  const [userData, _setUserData] = useState(emptypreferenciasUsuario);
  const [hoyosData, _setHoyosData] = useState({
    token:'',
    typeUser:'',
    typeDelivery:'',
    userHoyosName : null,
  });

  const hoyosDataRef = useRef(hoyosData);
  const userDataRef = useRef(userData);

  const setUserData = (data) => {
    // console.log('cambiando', data)
    userDataRef.current = data;
    _setUserData(data);
  }

  const setHoyosData = (data) => {
    hoyosDataRef.current = data;
    _setHoyosData(data);
  }

  const redirectToHome = () => {
    
    if (window.location.pathname != '/autenticacion') {
      window.location.href = '/autenticacion';
    }
  }



  //  Funciones para validar datos de usuario de hoyostools
  const getHoyosData = async() => {
    const token = localStorage.getItem(process.env.LOCAL_STORAGE_TOKEN_HOYOS);
    const tipoUsuario = localStorage.getItem(process.env.LOCAL_STORAGE_USER_TYPE_HOYOS);
    const tipoEntrega = localStorage.getItem(process.env.LOCAL_STORAGE_DELIVERY_TYPE_HOYOS);
    const nombreUsuario = localStorage.getItem(process.env.LOCAL_STORAGE_USER_NAME_HOYOS);

    //  Verifico si existen datos de hoyos para redireccionar al login si hace falta
    if(tipoUsuario && tipoEntrega && token){

      //  Si el usuario es un usuario logueado se debe verificar que exista su nombre de usuario
      if(tipoUsuario == "1"){
        if(!nombreUsuario){
          redirectToHome();
        }
      }

      
      setHoyosData({
        ...hoyosDataRef.current,
        token: token,
        typeUser: tipoUsuario,
        typeDelivery: tipoEntrega,
        userHoyosName: nombreUsuario || null
      });

      //  Valido que el token de hoyostools sea correcto
      await verifyToken(token);
      
    }else{
      redirectToHome();
    }

  }

  //  Función para verificar que el token es válido
  const verifyToken = async (tokenv) => {
  
    const request = {
      method: "POST",
      headers: new Headers({
        Authorization: `Bearer ${tokenv}`,
        "Content-Type": "application/x-www-form-urlencoded",
      }),
    };

    fetch(
      process.env.ENDPOINT_VALIDATE_TOKEN,
      request
    ) .then((response) => response.json())
      .then((data) => {
        if (data.error) {
          loginInvited();
        } else {
          setHoyosData({
            ...hoyosDataRef.current,
            token: tokenv
          });
          // alert("token válidooooo")
        }
      })
      .catch(() => {
        // Error en la petición, redirijo al home
        redirectToHome();
      });
  };
  
  //  Función para obtener un nuevo token en caso de vencimiento
  const loginInvited = async () => {
    const request = {
      method: "POST",
    };
  
    await fetch(
      process.env.ENDPOINT_GENERATE_INVITED_TOKEN,
      request
    )
      .then((response) => response.json())
      .then((data) => {
        console.log(!data.error);
        if (!data.error) {
          const tokenv = data.token;
          localStorage.setItem(process.env.LOCAL_STORAGE_TOKEN_HOYOS, tokenv);
          localStorage.removeItem(process.env.LOCAL_STORAGE_USER_NAME_HOYOS); // Se remueve el campo de nombre de usuario ya que en invitados no existe
          localStorage.setItem(process.env.LOCAL_STORAGE_USER_TYPE_HOYOS, "2");
          localStorage.setItem(process.env.LOCAL_STORAGE_DELIVERY_TYPE_HOYOS, 2); // El tipo de entrega se cambia a recoger en punto de venta

          setHoyosData({
            ...hoyosDataRef.current,
            token: tokenv,
            typeUser: 2,
            typeDelivery: 2,
            userHoyosName: null
          });

          // console.log("Se inció como invitado", "token: "+ tokenv)

        } else {
          // alert(`${data.message}`);
          redirectToHome();
        }
      })
      .catch((error) => {
        // Error en la petición, redirijo al home
        redirectToHome();
      });
  };


  //  UseEffect para ejecutarse al inicio de la aplicación
  useEffect(() => {

    async function getAllData (){

      // Obtengo los datos de sesión de Hoyostools y los valido
      await getHoyosData();

      //  Se buscan datos en el localStorage para cargarlos
      try {
        //  Verifico si los datos ya fueron consultados para no volver a ejecutar la funcion
        if (userData === emptypreferenciasUsuario) {
          const dataPreferencias = JSON.parse(localStorage.getItem('preferenciasUsuario'));
          if (dataPreferencias && dataPreferencias != {}) {
            setUserData(dataPreferencias);

            // Cambio el estado data verificada para quitar la vista de carga
            console.log('data verificada')
            setAllUserDataVerified(true);

          } else {

            //  Si no existe data debería redirigir a home pero en la vista de 
            //  personalización me puedo quedar para generar los datos
            if(window.location.pathname != '/personalizacion'){
              redirectToHome();
            }else{
              setAllUserDataVerified(true);
            }
            
          }
        }

      } catch (error) {
        console.log('error leyendo localstorage', error);
        redirectToHome();
      }
    }

    getAllData();


    //  Se escucha al evento de actualización de datos que proviene de character-personalizarion (AFRAME COMPONENT)
    document.addEventListener('update_user_prefs', updateUserPrefs, false);

    //  Se escucha al evento de actualización del color de piel (AFRAME COMPONENT)
    document.addEventListener('update_skin_color', updateSkinColor, false);

    //  Remover el eventlistener al desmontarse el componente
    return () => {
      document.removeEventListener('update_user_prefs', updateUserPrefs);
      document.removeEventListener('update_skin_color', updateSkinColor);
    }

  }, []);

  const updateUserPrefs = (e) => {

    let userDataCopy = { ...userDataRef.current };
    const dataPrenda = e.detail;
    // console.log('color', dataPrenda.color);

    if (userDataCopy.preferenciasUsuario && userDataCopy.preferenciasUsuario.ropa) {
      userDataCopy.preferenciasUsuario.ropa[dataPrenda.grupo] = {
        ...userDataCopy.preferenciasUsuario.ropa[dataPrenda.grupo],
        meshSelected: dataPrenda.prenda,
      }

      const datamaterials = userDataCopy.preferenciasUsuario.ropa.materiales;
      // console.log(datamaterials)
      if (datamaterials && datamaterials.length > 0) {
        const material = datamaterials.find(mat => mat.material === dataPrenda.material);

        if (material) {
          material.color = dataPrenda.color
        }
        else {
          datamaterials.push({
            material: dataPrenda.material,
            color: dataPrenda.color
          })
        }
      } else {
        userDataCopy.preferenciasUsuario.ropa.materiales = [
          {
            material: dataPrenda.material,
            color: dataPrenda.color
          }
        ]
      }

      let dataGroup = userDataCopy.preferenciasUsuario.ropa[dataPrenda.grupo];
      if (dataGroup.opciones) {
        let opcion = dataGroup.opciones.find(el => el.mesh === dataPrenda.prenda);
        if (opcion) {
          opcion.color = dataPrenda.color
        }
        else {
          dataGroup.opciones.push({
            mesh: dataPrenda.prenda,
            material: dataPrenda.material,
            color: dataPrenda.color
          })
        }

      }
      else {
        dataGroup.opciones = [{
          mesh: dataPrenda.prenda,
          color: dataPrenda.color
        }]
      }

    }

    setUserData(userDataCopy);

  }


  const updateSkinColor = (e) => {

    const color = e.detail.colorPiel;

    let userDataCopy = { ...userDataRef.current };
    userDataCopy.preferenciasUsuario.colorPiel = color;

    setUserData(userDataCopy);
    // console.log(userDataCopy);
  }


  if(!allUserDataVerified){
    return <LoadingPanel/>
  }

  return (
    <UserDataContext.Provider value={{ userData, setUserData, hoyosData }}>
      {children}
    </UserDataContext.Provider>
  );
}