import React, { useContext, useEffect, useState, useRef } from 'react';
import { Chip, Grid, IconButton, List, Typography } from "@mui/material";
import { Box } from "@mui/system";
import Input from '@mui/material/Input';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import InputAdornment from '@mui/material/InputAdornment';
import SendIcon from '@mui/icons-material/Send'

//  Importación de contextos
import { UserDataContext } from '../../../../context/UserDataContext';
import { MultiplayerContext } from '../../../../context/MultiplayerContext';

export const ChatMessageComponent = (props) => {

    const { message, user, isMine } = props;

    return(
        <Box sx={{
            width:'100%', 
            p:2,
            display:'flex', 
            flexDirection:'column', 
            justifyContent:'center', 
            alignItems: isMine? 'flex-end' :'flex-start'}}>
            <Chip
                label={<Typography variant="subtitle1" sx={{whiteSpace: 'normal', margin:'3px', wordWrap:'break-word'}}>{message}</Typography>}
                color={isMine? "primary" : "default"}
                href="#basic-chip"
                variant={isMine? "filled" : "outlined"}
                sx={{height:"100%"}}
                clickable={false}
            />


            <Typography variant="caption" display="block" gutterBottom>
                 {isMine? 'Enviado por mí': (user !== ''? 'Enviado por '+user : '')}
            </Typography>
        </Box>
        
        // <ListItem>
        //     <ListItemText primary={`Hola Juan`} />
        // </ListItem>
    );
}

export default function ChatContainer() {

    //  Datos de mensajes del contexto multijugador
    const { globalMessages, setGlobalMessages, chatData, addNewPrivateMessageMine, setGlobalMessagesCount, setConnectedUsers } = useContext(MultiplayerContext);

    // Contexto de mis datos de usuario
    const { userData }  = useContext(UserDataContext);

    const [message, setMessage] = useState("");

    const handleMessageChange = (event) => {
        setMessage(event.target.value);
    }

    const sendMessage = (event) => {
        // if (user && message) {
        event.preventDefault()
        if (message) {

            // console.log('Send!', message);
            if (NAF.connection.isConnected()) {

                if(chatData === 'global'){
                    const newMessage = { name: userData.preferenciasUsuario.nombre || 'SIN NOMBRE', message: message};
                    // console.log('enviando dat', newMessage);
                    NAF.connection.broadcastData("new_global_message", newMessage);
                    setGlobalMessages([...globalMessages, {...newMessage, isMine: true}]);
                }
                else{
                    const newMessage = { message: message };
                    NAF.connection.sendData(chatData.id, "new_private_message", newMessage);

                    //  Lo agrago a la lista de mis mensajes con la función proveniente de multiplayer context
                    addNewPrivateMessageMine(chatData.id, message);
                    // setGlobalMessages([...globalMessages, {...newMessage, isMine: true}]);
                }

                
            }
            else{
                alert('Lo sentimos, no se ha podido enviar tu mensaje')
            }

            setMessage('');
        } 
        else {
            // console.log("error")
        }
    }

    const messagesEndRef = useRef(null)

    //Escucho el conteo de los mensajes globales y si la ventana de chat está abierta quito la notificación
    useEffect(()=>{
        scrollToBottom();
    }, [globalMessages, chatData.messages])

    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
    }

    return (

        <Box component="form" onSubmit={sendMessage} 
            sx={{ height:'100%', display:'flex', flexDirection:'column', justifyContent:'space-between'}} >
                
            <Box id="chat-window" className="scroller" xs={12} sx={{height:'85%', overflow:'hidden', overflowY:'auto'}} item>
            
                <List id="chat-window-messages" >
                    { chatData == 'global'?
                        globalMessages.map((message, i) => 
                            <ChatMessageComponent 
                                key={i} 
                                isMine={message.isMine} 
                                message={message.message} 
                                user={message.sender}/>
                        )
                        :
                        chatData.messages?.map((message, i) => 
                            <ChatMessageComponent 
                                key={i} 
                                isMine={message.isMine} 
                                message={message.message} 
                                user={''}/>
                        ) || 'Losentimos, ha ocurrido un error al cargar los mensaje'
                    }

                    <div  ref={messagesEndRef}/>

                </List>

            </Box>
            <Grid xs={12} item>
            
                <FormControl fullWidth variant="standard">
                    <InputLabel htmlFor="standard-adornment-password">Mensaje...</InputLabel>
                    <Input
                        id="standard-adornment-password"
                        autoComplete='off'
                        type={'text' }
                        value={message}
                        onChange={handleMessageChange}
                        endAdornment={
                            <InputAdornment position="end">
                                <IconButton
                                    aria-label="toggle password visibility"
                                    type="submit"
                                    // onClick={handleClickShowPassword}
                                    // onMouseDown={handleMouseDownPassword}
                                >
                                <SendIcon /> 
                                </IconButton>
                            </InputAdornment>
                        }
                    />
                </FormControl>

            </Grid>
        </Box>


    );
}