/**
 * Contexto de React donde se manejan los eventos de conexiones multijugador
 * útil para proveer datos de los usuarios conectados a componentes como chat o lista de usuarios
 */

/**
 * POR HACER
 * 
 */

import React, { createContext, useContext, useEffect, useRef, useState } from 'react';

//  Importo el contexto de los datos de usuario
import { UserDataContext, emptypreferenciasUsuario } from './UserDataContext';

//  Defino una constante que será el almacen vacio de los datos
const emptyMultiplayerContext = {
    connectedUsers: [],
    avatarReady: false
}
export const MultiplayerContext = createContext({ ...emptyMultiplayerContext });

export function MultiplayerProvider({ children }) {

    //  Obtengo el contexto de los datos de usuario para acceder al nombre y otros datos para enviar
    const { userData } = useContext(UserDataContext);

    //  Listado de usuarios cnectados
    const [connectedUsers, _setConnectedUsers] = useState([]);
    const connectedUsersRef = useRef(connectedUsers);

    const setConnectedUsers = (data) => {
        // console.log('cambiando', data)
        connectedUsersRef.current = data;
        _setConnectedUsers(data);
    }

    //  Estado multiplayer
    const [multiplayerData, _setMultiplayerData] = useState({ ...emptyMultiplayerContext });
    const multiplayerDataRef = useRef(multiplayerData);

    const setMultiplayerData = (data) => {
        // console.log('cambiando', data)
        multiplayerDataRef.current = data;
        _setMultiplayerData(data);
    }


    //  Estado mensajes Globales
    const [globalMessages, _setGlobalMessages] = useState([]);
    const globalMessagesRef = useRef(globalMessages);

    const setGlobalMessages = (data) => {
        // console.log('cambiando', data)
        globalMessagesRef.current = data;
        _setGlobalMessages(data);
    }


    const [globalMessagesCount, _setGlobalMessagesCount] = useState(0);
    const globalMessagesCountRef = useRef(globalMessagesCount);

    const setGlobalMessagesCount = (data) => {
        // console.log('cambiando', data)
        globalMessagesCountRef.current = data;
        _setGlobalMessagesCount(data);
    }

    //  Estado que controla el contenedor de mensajes que se va a mostrar
    const [chatData, _setChatData] = useState('global');

    function setChatData(userId){

        if(userId === 'global'){
            _setChatData('global');
        }else{
            const selectedUser = connectedUsers.find(user => user.id === userId);
            if(selectedUser){
                //console.log('usuario seleccionado', selectedUser);
                _setChatData(selectedUser);
            }
        }
        

    }

    function onNewPrivateMessage (senderId, dataType, data, targetId){
        //  Creo una copia de la lista de usuarios original para después modificarlo y setearlo
        let usersDataCopy = connectedUsersRef.current.length>0? [...connectedUsersRef.current] : [];
        const senderUser = usersDataCopy.find(user => user.id === senderId);
        if(senderUser){

            //  Agrego el nuevo mensaje con la bandera de is mine diferente
            senderUser.messages.push({ message: data.message, isMine:false});

            //  Incremento el conteo de mensajes
            senderUser.messagesCount += 1;

            setConnectedUsers(usersDataCopy);
        }
    }

    function addNewPrivateMessageMine (userId, message) {
        let usersDataCopy = connectedUsersRef.current.length>0? [...connectedUsersRef.current] : [];
        const senderUser = usersDataCopy.find(user => user.id === userId);
        if(senderUser){

            //  Agrego el nuevo mensaje con la bandera de is mine diferente
            senderUser.messages.push({ message: message, isMine:true});
            setConnectedUsers(usersDataCopy);
        }
    }

    function onNewGlobalMessage (senderId, dataType, data, targetId) {
        // console.log('mensaje Global Nuevo de ' + senderId, data)
        let globalMessagesCopy = [... globalMessagesRef.current]
        globalMessagesCopy.push({sender:data.name, message: data.message, isMine:false});
        
        setGlobalMessages(globalMessagesCopy);
        setGlobalMessagesCount(globalMessagesCountRef.current+1);
    }
    

    function connectMultiplayer (e) {

        const avatarReady = e.detail.ready;
        if (avatarReady) {
            setMultiplayerData({ ...multiplayerDataRef.current, avatarReady: true })
            //  Una vez el avatar ha cargado se conecta a la escena multijugador
            if (!NAF.connection.isConnected()) {
                AFRAME.scenes[0].emit('connect');
            }
            // 
        }
    }
    

    function onNewUserConnected(e)  {
        NAF.connection.sendData(e.detail.clientId, "new_user_connected", { nombre: userData.preferenciasUsuario.nombre })
    }

    function createNewUserInList (senderId, dataType, data, targetId) {
        // //  Creo una copia del estado y agrego el nuevo usuario a la lista para luego setear el estado global del contexto
        // const multiplayerDataCopy = { ...connectedUsersRef.current }

        // //  Verifico si el usuario ya está agregado
        // const existUser = multiplayerDataCopy.connectedUsers.find(user => user.id === senderId);

        // if (existUser === undefined || existUser === null) {
            
        //     // let arrayUsers = [...multiplayerDataCopy.connectedUsers];
        //     // arrayUsers.push({ id: senderId, userName: data.nombre });
        //     // multiplayerDataCopy.connectedUsers = arrayUsers;
        //     multiplayerDataCopy.connectedUsers.push({ id: senderId, userName: data.nombre });
        //     setMultiplayerData(multiplayerDataCopy);
        // }

        //  Creo una copia del estado y agrego el nuevo usuario a la lista para luego setear el estado global del contexto
        let usersListCopy = connectedUsersRef.current.length>0? [...connectedUsersRef.current] : [];
        console.log('lista actual', JSON.stringify(usersListCopy));

        //  Verifico si el usuario ya está agregado
        const existUser = usersListCopy.length> 0 ? usersListCopy.find(user => user.id === senderId): null;

        if (existUser === undefined || existUser === null) {
            
            usersListCopy.push({ id: senderId, userName: data.nombre, messages:[], messagesCount:0 });
            setConnectedUsers(usersListCopy);
        }
    }

    function onUserDisconnected (e){
        
        // const multiplayerDataCopy = { ...multiplayerDataRef.current }

        // multiplayerDataCopy.connectedUsers = multiplayerDataCopy.connectedUsers.filter(user => user.id !== e.detail.clientId);
        // setMultiplayerData(multiplayerDataCopy);

        let usersListCopy = connectedUsersRef.current.length>0? [...connectedUsersRef.current] : [];

        usersListCopy = usersListCopy.filter(user => user.id !== e.detail.clientId);
        setConnectedUsers(usersListCopy);
    }

    useEffect(() => {
        document.addEventListener('avatar_ready', connectMultiplayer);

        //  Valido que el nombre de usuario exista para poder enviarlo
        if (userData !== emptypreferenciasUsuario) {
            //  Se espera a que el jugador esté conectado para escuchar los demás eventos multijugador
            document.body.addEventListener('connected', function (event) {
                // console.log('mi nombre ', userData.preferenciasUsuario)

                //  Envío mi nombre de usuario a los demás clientes para ser agregado a la lista
                NAF.connection.broadcastData("new_user_connected", { nombre: userData.preferenciasUsuario.nombre })

                //  Escucho al evento que trae el nombre de los usuarios conectados para añadirlo a la lista de chats
                NAF.connection.subscribeToDataChannel("new_user_connected", createNewUserInList);

                //  Cada vez que un usuario nuevo se conecta le envío mi nombre para que me agregue
                document.body.addEventListener('clientConnected', onNewUserConnected);   

                //  Cuando un usuario se desconecta lo remuevo del arreglo de usuarios conectados
                document.body.addEventListener('clientDisconnected', onUserDisconnected, false);
                // document.body.addEventListener('entityRemoved', onUserDisconnected);


                /***********************MENSAJES***********************/
                NAF.connection.subscribeToDataChannel("new_global_message", onNewGlobalMessage);
                NAF.connection.subscribeToDataChannel("new_private_message", onNewPrivateMessage);
            });
        }


        // Devolvemos una función para anular la suscripción a los eventos
        return () => {
            document.removeEventListener("avatar_ready", connectMultiplayer);
            document.body.removeEventListener('clientConnected', onNewUserConnected);
            document.body.removeEventListener('clientDisconnected', onUserDisconnected);
        }
    }, [userData]);


    return (
        <MultiplayerContext.Provider value={{ 
            multiplayerData, setMultiplayerData, 
            globalMessages, setGlobalMessages,
            globalMessagesCount, setGlobalMessagesCount,
            connectedUsers, setConnectedUsers,
            chatData, setChatData, addNewPrivateMessageMine }}>
            {children}
        </MultiplayerContext.Provider>
    );
}