import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CardMedia from '@mui/material/CardMedia';
import Toolbar from '@mui/material/Toolbar';
import Container from '@mui/material/Container';
import AdbIcon from '@mui/icons-material/Adb';


//  Componentes React
import AvatarMenu from '../AvatarMenu/AvatarMenu';

const Header = ({children}) => {
    return (
        <AppBar  position="static">
            <Container maxWidth="xl">
                <Toolbar className='interactable-ui' disableGutters sx={{display:'flex', justifyContent:'space-between'}}>
                    {/* <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} /> */}
                    <CardMedia
                        component="img"
                        sx={{ height:'2rem',width:'8rem',objectFit:'contain' , display: { xs: 'none', md: 'flex' }, mr: 1, p:0 }}
                        image={require('../../../assets/images/logos_loacles/logo_Hoyostools.png')}
                    />
                    <Box sx={{display:'flex', justifyContent:'flex-end', alignItems:'center',width:{ xs: '100%', md: 'auto' } }}>
                        {children}
                        <AvatarMenu/>
                    </Box>
                </Toolbar>
            </Container>
        </AppBar>
    );
};
export default Header;