/**
 * POR HACER
 * Incluir movimiento con wasd
 * Incluir movimiento con móvil (Joystick o botón)
 */
//  Controles de movimiento del jugador

AFRAME.registerComponent('movement-controls-super', {

    /*******************************************************************
     * Schema
     */

    dependencies: ['rotation'],

    schema: {
        enabled:            { default: true },
        controls:           { default: ['keyboard-touch'] },
        speed:              { default: 2, min: 0 },
        camera:             { default: '[movement-controls] [camera]', type: 'selector' },
        characterModel:     {type: 'selector', default: '#model'},
        animationIdle:      {type: 'string', default: 'idle'},
        animationWalking:   {type: 'string', default: 'walking'},
        forwardButton:       {type: 'selector', default:'#button-forward'}
    },

    /*******************************************************************
     * Lifecycle
     */

    init: function() {
      var el = this.el;
      this.activeVelocity = false

      // velocidad para activar el movimiento, en este caso se aplica en cada eje con 1 o -1
      this.velocity = new THREE.Vector3();

      this.bindMethods();

      this.characterModel = this.data.characterModel || null;
      this.cameraEl = this.data.camera || null;
      this.buttonForwardEl = this.data.forwardButton || document.querySelector('#button-forward')
    
    },

    play: function () {
      this.addEventListeners();
    },

    pause: function () {
        this.removeEventListeners();
    },

    remove: function () {
        this.pause();
    },

    bindMethods: function () {
        this.onKeyDown = this.onKeyDown.bind(this);
        this.onKeyUp = this.onKeyUp.bind(this);
        this.onTouchStart = this.onTouchStart.bind(this);
        this.onTouchEnd = this.onTouchEnd.bind(this);
    },

    addEventListeners: function () {
      const sceneEl = this.el.sceneEl;
      const canvasEl = sceneEl.canvas;
  
      
      if (!canvasEl) {
        sceneEl.addEventListener('render-target-loaded', this.addEventListeners.bind(this));
        return;
      }

      //  Sin esto no se pueden escuchar eventos en el canvas
      canvasEl.setAttribute('tabindex','1') 

      canvasEl.addEventListener('keydown', this.onKeyDown, false);
      canvasEl.addEventListener('keyup', this.onKeyUp, false);

      document.addEventListener('stopCharacter', ()=>{ this.disableMovement()}, false)

      if(this.buttonForwardEl){
        console.log("boton", this.buttonForwardEl)
        this.buttonForwardEl.addEventListener('touchstart', this.onTouchStart);
        this.buttonForwardEl.addEventListener('touchend', this.onTouchEnd);
      }
    },
    
    removeEventListeners: function () {

      const sceneEl = this.el.sceneEl;
      const canvasEl = sceneEl.canvas;
      if (!canvasEl || !sceneEl) { return; }
  
      canvasEl.removeEventListener('keydown', this.onKeyDown);
      canvasEl.removeEventListener('keyup', this.onKeyUp);
      this.buttonForwardEl.removeEventListener('touchstart', this.onTouchStart);
      this.buttonForwardEl.removeEventListener('touchend', this.onTouchEnd);
    },

    onKeyDown: function (e) {
        const keyName = e.code;
        // console.log("tecla", keyName || keyName=="KeyW" || keyName=="ArrowUp")
        if(keyName==="KeyW" || keyName==="ArrowUp"){
          // velocidad para activar el movimiento, en este caso se aplica en cada eje con 1 o -1
          // this.activeVelocity = true;
          this.velocity.z = -1;  
        }

        if(keyName==="KeyS" || keyName==="ArrowDown"){
          // velocidad para activar el movimiento, en este caso se aplica en cada eje con 1 o -1
          // this.activeVelocity = true;
          this.velocity.z = 1;  
        }

        if(keyName==="KeyA" || keyName==="ArrowLeft"){
          // this.activeVelocity = true;
          this.velocity.x = -1;  
        }

        if(keyName==="KeyD" || keyName==="ArrowRight"){
          // this.activeVelocity = true;
          this.velocity.x = 1;  
        }

        // e.preventDefault();
    },

    onKeyUp: function (e) {
        const keyName = e.code;
        if( keyName==="KeyW" || keyName==="ArrowUp" || 
            keyName==="KeyS" || keyName==="ArrowDown"
          )
        {
          // this.activeVelocity = false;
          this.velocity.z = 0;
          this.stopCharacter();
        }

        if(keyName==="KeyA" || keyName==="ArrowLeft" ||
          keyName==="KeyD" || keyName==="ArrowRight"
        )
        {
          // this.activeVelocity = false;
          this.velocity.x = 0;
          this.stopCharacter();
        }
        // e.preventDefault();
    },

    onTouchStart: function (e) {
      // this.activeVelocity = true;
      e.preventDefault();
    },

    onTouchEnd: function (e) {
      // this.activeVelocity = false;
      this.stopCharacter();
      e.preventDefault();
    },

    moveCharacter: function(){
      
      let aux_velocity = new THREE.Vector3(this.velocity.x, this.velocity.y, this.velocity.z);
      // Se obtiene la rotación de la cámara para aplicar velocidad dependeiendo de la vista de la cámara
      const quaternion = new THREE.Quaternion();
      quaternion.copy(this.data.camera.object3D.quaternion);
      aux_velocity.applyQuaternion(quaternion);
      
      if(this.el.body){

        //  Se crea un impulso y se aplica como velocidad al body del jugador
        let resultantImpulse = new Ammo.btVector3( aux_velocity.x, 0, aux_velocity.z )
        resultantImpulse.op_mul(this.data.speed);
        this.el.body.setLinearVelocity( resultantImpulse );


        if(this.characterModel){

          // let horizontalRotation = 0;
          // if(Math.abs(this.velocity.x)>0){
          //   horizontalRotation = Math.PI/2 * this.velocity.x;
          // }

          // let frontRotation = 0
          // if(this.velocity.z < 0){
          //   frontRotation = Math.PI;
          // }

          //  Se rota el modelo hacia donde apunta la cámara 
          if(this.cameraEl){
            this.characterModel.object3D.rotation.y = this.cameraEl.object3D.rotation.y + Math.PI;
            // this.characterModel.object3D.rotation.y = this.cameraEl.object3D.rotation.y + frontRotation + horizontalRotation;
          }
          
          //  Se activa la animación de caminar
          this.characterModel.setAttribute('animation-mixer', {
            clip: this.data.animationWalking
          });
        }
      }
    },

    disableMovement: function() {
      
      this.velocity.x = 0;
      this.velocity.z = 0;
      this.stopCharacter();
      
    },

    stopCharacter: function() {
      
      if(this.el.body){

        //  Se crea un impulso y se aplica como velocidad al body del jugador
        let resultantImpulse = new Ammo.btVector3( 0, 0, 0 )
        resultantImpulse.op_mul(this.data.speed);
        this.el.body.setLinearVelocity( resultantImpulse );


        if(this.characterModel){
          //  Se activa la animación de reposo
          this.characterModel.setAttribute('animation-mixer', {
            clip: this.data.animationIdle
          });
        }
      }
      
    },

    tick: function(){
      //  console.log(this.activeVelocity);
      if(this.velocity.x || this.velocity.z){
        this.moveCharacter();
      }else{
        // El freno del jugador se aplica en onKeyUp para evitar que la velocidad se quede siempre en 0
      }
    }
  
});  





