import React, { useEffect, useState } from 'react';

//  Componentes MUI
import { Box, Button, Typography, CardMedia } from '@mui/material';

export default function LocalInfoContainer({selectedLocal = 'beea88cd-ff6e-4c47-9035-8a85f41b13f3'}) {

  const handleEntry = () => {
    // REDIRIGIR AL LOCAL
    window.location.href = '/local?ID=' + selectedLocal;
  }

  return (
    <Box
      sx={{ height: '100%', p: 3, textAlign: 'center', display: 'flex', flexFlow: 'column', justifyContent: 'center', alignItems: 'center' }}
      role="presentation"
    >
      <Box id="chat-window" className="scroller" xs={12} 
        sx={{
          height:'95%', 
          overflow:'hidden', 
          overflowY:'auto',
          display:'flex',
          flexDirection:'column',
          alignItems:'center'
        }} 
        item>
        {/* <Typography variant="h4" gutterBottom component="div">
          LOGO
        </Typography> */}
        <CardMedia
          component="img"
          sx={{ width:'80%', objectFit:'contain' , mb:3, p:0 }}
          image={require('../../../../assets/images/logos_loacles/LOGOS-02.png')}
        />
        <Typography sx={{mt:2}} variant="button" gutterBottom component="div">
          Bienvenido
          <br></br>
          para ingresar al local haga click sobre el botón "entrar"
        </Typography>

        <Button variant="outlined" sx={{ mt: 3 }} onClick={handleEntry}>Entrar</Button>
      </Box>
    </Box>
  );
}