//  Componente que detecta las colisiones con objetos de teletransportacion a los locales

AFRAME.registerComponent('local-teleporter', {

    schema: {
        dataLocal:       {type: 'string', default: '{}'}
    },

    init: function(){
        let el = this.el;

        const localData = JSON.parse(this.data.dataLocal)
        // console.log('teleporter data', localData)

        // al detectar colisión se envia el evento para abrir info del local
        el.addEventListener('collidestart', (e) => {
            // console.log('entrooooo',e.detail.targetEl.id);
            //  Verifico que la colisión es con un jugador para activar el drawer
            if(e.detail.targetEl.id==='player-entity'){
                const openLocalInfoEvent = new CustomEvent('open_local_info');
                document.dispatchEvent(openLocalInfoEvent);

                const setDrawerLocalData = new CustomEvent('select_local', {
                    detail: {id_local: localData.id_local}
                });
                document.dispatchEvent(setDrawerLocalData);

                const stopMovementEvent = new CustomEvent('stopCharacter');
                document.dispatchEvent(stopMovementEvent);
            }
        })

        // al detectar colisión se envia el evento para cerrar info del local
        el.addEventListener('collideend', (e) => {
            // console.log('salioooooo',e.detail.targetEl.id);
            //  Verifico que la colisión es con un jugador para activar el drawer
            if(e.detail.targetEl.id==='player-entity'){
                const closeLocalInfoEvent = new CustomEvent('close_local_info');
                document.dispatchEvent(closeLocalInfoEvent);
            }
            
        })
        
    }
});