/**
 * Creación de materiales y aplicación para el modelo del centro comercial
 * 
 */


AFRAME.registerComponent('hide-glb-parts', {
    schema: {
        partsToHide: {type: 'array', default: []}
    },

    init: function(){
        const el = this.el;
        const partsToHide = this.data.partsToHide;
        // console.log('modelo data', partsToHide);

        el.addEventListener("model-loaded", e =>{
            let mesh = el.getObject3D('mesh');

            partsToHide.forEach(element => {
                const part = el.object3D.getObjectByName(element);
                if(part){
                    console.log('parte eliminada', part.name)
                    part.parent.remove(part);
                }
            });

        });
    },
  
});

