import { borderRight } from '@mui/system';
import React, { useState } from 'react';
import ReactNipple from 'react-nipple';

const SizeJoystick = 80;

export default function SearchInput() {

    const [ sceneCanvas, setSceneCanvas ] = useState(document.querySelector('a-scene').canvas)

    const handleMove = (evt, data)=> {

        if(!data.direction || !data.direction.angle) return;
        // const sceneCanvas = 
        if(sceneCanvas){

            switch (data.direction.angle) {

                case 'up':
                    sceneCanvas.dispatchEvent(new KeyboardEvent('keydown',  {'code':'KeyW'}));
                    break;
                case 'down':
                    sceneCanvas.dispatchEvent(new KeyboardEvent('keydown',  {'code':'KeyS'}));
                    break;
                case 'right':
                    sceneCanvas.dispatchEvent(new KeyboardEvent('keydown',  {'code':'KeyD'}));
                    break;
                case 'left':
                    sceneCanvas.dispatchEvent(new KeyboardEvent('keydown',  {'code':'KeyA'}));
                    break;
            }
        }
        else{
            setSceneCanvas(document.querySelector('a-scene').canvas);
        }
        
    }

    const handleStop = (evt, data)=> {
        removeEvents();
    }

    const handleEvent = (evt, data) => {
        console.log(evt, data);
    };

    const removeEvents = () => {
        if(sceneCanvas){
            sceneCanvas.dispatchEvent(new KeyboardEvent('keyup',  {'code':'KeyW'}));
            sceneCanvas.dispatchEvent(new KeyboardEvent('keyup',  {'code':'KeyA'}));
            sceneCanvas.dispatchEvent(new KeyboardEvent('keyup',  {'code':'KeyS'}));
            sceneCanvas.dispatchEvent(new KeyboardEvent('keyup',  {'code':'KeyD'}));
        }
        else{
            setSceneCanvas(document.querySelector('a-scene').canvas);
        }
        
    }

    return (
        <div className='interactable-ui'>
            <ReactNipple
                options={{ mode: 'static', position: { top: '50%', left: '50%' }, }}
                style={{
                    // outline: '1px dashed red',
                    color: 'blue',
                    width: 150,
                    height: 150,
                    position: 'absolute',
                    bottom:'10vh',
                    right: '1rem'
                }}
                // onStart={handleMove}
                onEnd={handleStop}
                onMove={handleMove}
                onDir={removeEvents}
            />
        </div>
    );
}