/**
 * POR HACER
 * cargar la imagen desde el src y no agregar un item en los assets
 * la creación de elementos adicionales puede relentizar la experiencia
 */

AFRAME.registerComponent('adjust-image', {

    schema: {
        maxW: {type: 'number', default: 0.3},
        maxH: {type: 'number', default: 0.3},
        srcImage: {string: 'string', default: ''}
    },
  
    init: function() {
        let el = this.el;
        // console.log('imagen',this.data.srcImage )

        const urlImage = this.data.srcImage//el.getAttribute('src')

        const {maxH, maxW} = this.data;
        // console.log('dataimage', this.data)

        let randomId = Math.floor(Math.random() * Date.now())
        const new_image_asset = document.createElement('img');
        new_image_asset.setAttribute('id', "product_image_"+randomId); // Create a unique id for asset
        new_image_asset.setAttribute('src', urlImage);
        new_image_asset.setAttribute('crossorigin', "anonymous");
        document.querySelector("#aframe-assets").appendChild(new_image_asset);

        new_image_asset.addEventListener("load", function(){
        
            const h = this.naturalHeight;
            const w = this.naturalWidth;
            const aspect = w/h
            const containerAspect = maxW/maxH
        
            if(containerAspect > aspect){
                el.setAttribute('height', maxH);
                el.setAttribute('width', maxH*aspect);
            }else{
                el.setAttribute('width', maxW);
                el.setAttribute('height', maxW/aspect);
            }
            el.setAttribute('src', '#'+"product_image_"+randomId);
        });

        // el.setAttribute('scale', ' 0.8 0.8 0.8');

        // el.setAttribute('animation__mouseEnter', 
        //     'property: scale; type: scale; from: 0.8 0.8 0.8; to: 0.85 0.85 0.85; startEvents: mouseenter; dur: 200');
        // el.setAttribute('animation__mouseLeave', 
        //     'property: scale; type: scale; from: 0.85 0.85 0.85; to: 0.8 0.8 0.8; startEvents: mouseleave; dur: 200');
        // animation__mouseEnter="property: scale; type: scale; from: 1 1 1; to: 1.1 1.1 1.1; startEvents: mouseenter; dur: 200"
        // animation__mouseLeave="property: scale; type: scale; from: 1.1 1.1 1.1; to: 1 1 1; startEvents: mouseleave; dur: 200"
    },
  
});