import React from 'react';
import Box from '@mui/material/Box';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MuiAppBar from '@mui/material/AppBar';
import { styled } from '@mui/material/styles';
import Drawer from '@mui/material/Drawer';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

//  Componente personaluzado de MuiAppBar
export const AppBar = styled(MuiAppBar, {
    shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
    }),
    ...(open && {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    }),
}));

const DrawerHeader = styled('div')(({ theme }) => ({
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-start',
}));

export default function LateralDrawer ({ open, closeDrawer, title, children }) {
    const theme = useTheme();
    const matches = useMediaQuery(theme.breakpoints.up('sm'));

    const handleCloseChat = () => {
        closeDrawer();
    };

    return (
        <Drawer
            PaperProps={{ sx: { height: "90%" } }}
            anchor="right"
            open={open}
            onClose={handleCloseChat}
        >
            <DrawerHeader>
                <IconButton onClick={handleCloseChat}>
                    <ChevronRightIcon />
                </IconButton>
                <Typography sx={{ml:1}} variant="h6" gutterBottom component="div">
                    {title}
                </Typography>
            </DrawerHeader>

            <Divider />
            <Box
                sx={{ width: matches ? 400 : '100vw', height: '90%', p: 3 }} //  'auto' : 
            >
                {children}

            </Box>
        </Drawer>
    );

}
