/**
 * Aquí se realizan la operaciones necesarias para mantener el estado de multijugador
 * Se añaden las propiedades del avatar que se desean compartir con Networked AFrame
 */

//  esperar a que el documento se cargue, entre ellos esta la carga de assets donde estan los templates de naf
window.addEventListener('DOMContentLoaded', (event) => {

  //  Se agrega el esquema para seguir las propiedades del avatar en multijugador
  //  Para hacerlo se crea el template dentro de a-assets en html
  //  Dentro del tenplate se especifican los componentes inicialmene vacios que se van a compartir en multijugador
  //  Luego dentro de la entidad con el componente networked se le añade una entidad similar a la que se define en el avatar template
  //  Los cambios que se apliquen en la entidad con networked también se aplicarán en la entidad remota multijugador
  NAF.schemas.getComponentsOriginal = NAF.schemas.getComponents;
  NAF.schemas.getComponents = (template) => {
    if (!NAF.schemas.hasTemplate("#avatar-template")) {

      NAF.schemas.add({
        template: '#avatar-template',
        components: [
          {
            component: "position",
            // requiresNetworkUpdate: NAF.utils.vectorRequiresUpdate(0.001)
          },
          {
            component: "rotation",
            // requiresNetworkUpdate: NAF.utils.vectorRequiresUpdate(0.5)
          },
          {
            selector: '.naf-avatar-model',
            component: 'rotation',
          },
          {
            selector: '.naf-avatar-model',
            component: 'gltf-model',
          },
          {
            selector: '.naf-avatar-model',
            component: 'character-textures',
          },
          {
            selector: '.naf-avatar-model',
            component: 'load-character-prefs',
          }
          ,
          {
            selector: '.naf-avatar-model',
            component: 'animation-mixer',
            // property: 'clip'
          }
          ,
          {
            selector: '.naf-avatar-name',
            component: 'text',
            property: 'value'
          }
        ]
      });

    }
    const components = NAF.schemas.getComponentsOriginal(template);
    return components;
  }

});

