const axios = require('../../../../modules/axios.min').default; 

AFRAME.registerComponent('mall-data', {
    //localsData
    schema: {
        localsData: {type: 'string', default: '[]'},
    },
    init: function(){
        let el = this.el;
        const localsData = JSON.parse(this.data.localsData)

        console.log('data locales', localsData);

        //Puede ser que el modelo ya haya cargado
        let mesh = el.getObject3D('mesh');
        if(mesh){
            this.applyData(localsData, mesh)
        }else{
            el.addEventListener("model-loaded", e =>{
                mesh = el.getObject3D('mesh');
                
                if (!mesh){return;}   
                this.applyData(localsData, mesh)
            });
        }
        
    },

    applyData: function(localsData, mesh){
        // const local = localsData[0]

        localsData.forEach(local => {
            const logo_local_entity = document.querySelector('#logo-local-'+ local.clave_local);
            if(local.imagen_aviso_local && local.imagen_aviso_local!=='' && local.imagen_aviso_local !=='imagen_aviso_local' && logo_local_entity){
                
                logo_local_entity.setAttribute('adjust-image',{
                    maxW: logo_local_entity.getAttribute('width'),
                    maxH: logo_local_entity.getAttribute('height'),
                    srcImage: process.env.URL_AMAZON_FILES + local.imagen_aviso_local,
                })

                logo_local_entity.setAttribute('visible', true);
            }
            
            const localTexture = local.textura_local && local.textura_local!==''? 
                new THREE.TextureLoader().load( require('../../../assets/images/textures/locales/' + local.textura_local))
                :
                null;

            if(localTexture){
                localTexture.wrapS = THREE.RepeatWrapping;
                localTexture.wrapT = THREE.RepeatWrapping;
                localTexture.repeat.set(50, 50)
                localTexture.rotation = 0.875;
            }
            
            const mat = new THREE.MeshStandardMaterial({
                roughness: 1,
                metalness:0.1,
                map: localTexture,
                color: local.color_local && local.color_local !==''?
                        local.color_local : '#ffffff'
            });

            mesh.traverse(function(node){
                        
                if (node.isMesh){  
                    if(node.material.name === 'Local'+ local.clave_local){
                        node.material = mat;
                    }
                }
            });


            //  Cofigurar el teletrasnsportador para cada local
            const teleporter_local_entity = document.querySelector('#teleporter-local-'+ local.clave_local);
            if(teleporter_local_entity){
                teleporter_local_entity.setAttribute('local-teleporter',{
                    dataLocal: JSON.stringify({id_local : local.id_local})
                })
            }
        });

        // const logo_local_1_entity = document.querySelector('#logo-local-1');
        // if(local.imagen_aviso_local && local.imagen_aviso_local!=='' && local.imagen_aviso_local !=='imagen_aviso_local'){
            
        //     logo_local_1_entity.setAttribute('adjust-image',{
        //         maxW: logo_local_1_entity.getAttribute('width'),
        //         maxH: logo_local_1_entity.getAttribute('height'),
        //         srcImage: process.env.URL_AMAZON_FILES + local.imagen_aviso_local,
        //     })

        //     logo_local_1_entity.setAttribute('visible', true);
        // }
        
        // const localTexture = local.textura_local && local.textura_local!==''? 
        //     new THREE.TextureLoader().load( require('../../../assets/images/textures/locales/' + local.textura_local))
        //     :
        //     null;

        // if(localTexture){
        //     localTexture.wrapS = THREE.RepeatWrapping;
        //     localTexture.wrapT = THREE.RepeatWrapping;
        //     localTexture.repeat.set(50, 50)
        //     localTexture.rotation = 0.875;
        // }
        
        // const mat = new THREE.MeshStandardMaterial({
        //     roughness: 1,
        //     metalness:0.1,
        //     map: localTexture,
        //     color: local.color_local && local.color_local !==''?
        //             local.color_local : '#ffffff'
        // });

        // mesh.traverse(function(node){
                    
        //     if (node.isMesh){  
        //         switch(node.material.name) {
        //             case "Local1":
        //                 node.material = mat;
        //                 // node.material.color.set(local.color_local)
        //                 break;

        //         }
        //     }
        // });
    }
  
});


